@media (max-width: 736px) { #header { padding-top: 20px; height: 70px; background: #fff; }
  #header .header_logo, #header .gnb_btn { top: 20px; } }

#header.fixed { padding-top: 20px; }

@media (max-width: 1520px) { #header.fixed { background: #fff; } }

#header.fixed .header_logo, #header.fixed .gnb_btn { top: 20px; }

#header.gnb_on { background: none !important; }

#header.gnb_on .lnb_d1_wrap > li.on > a, #header.gnb_on .lnb_d1_wrap > li:hover > a, #header.gnb_on .lnb_d2_wrap > li.on > a, #header.gnb_on .lnb_d2_wrap > li:hover > a { color: #fff; }

@media (max-width: 736px) { #header.gnb_on .lnb_d1_wrap { display: block; } }

.has_wnd_nav #header.fixed { background: #fff; }

.header_logo { position: absolute; top: 58px; left: 20px; -webkit-transition: top, 0.4s; -khtml-transition: top, 0.4s; -moz-transition: top, 0.4s; -ms-transition: top, 0.4s; -o-transition: top, 0.4s; transition: top, 0.4s; display: inline-block; width: 103px; cursor: pointer; }

@media (max-width: 736px) { .header_logo { width: 80px; } }

.gnb_btn { position: absolute; top: 58px; right: 20px; -webkit-transition: top, 0.4s; -khtml-transition: top, 0.4s; -moz-transition: top, 0.4s; -ms-transition: top, 0.4s; -o-transition: top, 0.4s; transition: top, 0.4s; width: 46px; height: 34px; cursor: pointer; }

.gnb_btn, .gnb_btn .line { display: inline-block; -webkit-transition: all 0.4s; -khtml-transition: all 0.4s; -moz-transition: all 0.4s; -ms-transition: all 0.4s; -o-transition: all 0.4s; transition: all 0.4s; }

.gnb_btn .line { position: absolute; right: 0; width: 100%; height: 4px; background-color: #000; border-radius: 4px; }

.gnb_btn .line:nth-child(1) { width: 70%; top: 0; }

.gnb_btn .line:nth-child(2) { width: 100%; top: 16px; }

.gnb_btn .line:nth-child(3) { width: 70%; bottom: 0; }

.gnb_btn.on .line { background-color: #fff; }

.gnb_btn.on .line:nth-child(1) { -webkit-transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); -khtml-transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); -moz-transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); -ms-transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); -o-transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); transform: translate(0, 14px) rotate(-45deg) scale(1.3, 1); }

.gnb_btn.on .line:nth-child(2) { filter: alpha(opacity=0); -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)"; -webkit-opacity: 0; -khtml-opacity: 0; -moz-opacity: 0; -ms-opacity: 0; -o-opacity: 0; opacity: 0; }

.gnb_btn.on .line:nth-child(3) { -webkit-transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); -khtml-transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); -moz-transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); -ms-transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); -o-transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); transform: translate(0, -16px) rotate(45deg) scale(1.3, 1); }

